import React, { useState, useEffect } from 'react'
import Fade from 'react-reveal/Fade';

import './Stacks.css'
import stacks from '../stacks-data'

const Stack = () => {

    // use state - stacks data & window size
    const [index, setIndex] = useState(1)
    const [size, setSize] = useState(window.innerWidth)
    
    // checks window size
    const checkSize = () => {
        setSize(window.innerWidth)
    }

    // use effect window
    useEffect(() => {
        // event listener
        window.addEventListener('resize', checkSize)

        // cleanup
        return () => {
            window.removeEventListener('resize',checkSize)
        }
    })

    // next handler
    const nextHandler = () => {
        let nextIndex = index + 1
        if (nextIndex > 3) {
            nextIndex = 1
        }
        setIndex(nextIndex)
    }

    // prev handler
    const prevHandler = () => {
        let prevIndex = index - 1
        if (prevIndex < 1) {
            prevIndex = 3
        }
        setIndex(prevIndex)
    }

    // current stack
    const currentStack = stacks.filter(stack => stack.id === index)

    // component
    return (
        <section className="stacks">
            <h2 className="subheader-white">Tech Stacks</h2>
            <Fade bottom>
                <StackContainer
                    currentStack={currentStack}
                    nextHandler={nextHandler}
                    prevHandler={prevHandler}
                    size={size}
                />
            </Fade>
        </section>
    )
}

// Stack Container
const StackContainer = ({ currentStack, nextHandler, prevHandler, size }) => {

    // destructure stack data
    const { name, status, language, tech_stack } = currentStack[0]

    return (
        <div className="stacks-container">
                
            {/* Stacks Slider */}
            <div className="stacks-slider">
                <img 
                    className="left-slider" 
                    src={process.env.PUBLIC_URL + '/images/icons/arrow-left.svg'} 
                    alt="left"
                    onClick={() => prevHandler()}    
                />
                <span>{name}<p className="stacks-status">{status}</p></span>
                <img
                    className="right-slider"
                    src={process.env.PUBLIC_URL + '/images/icons/arrow-right.svg'}
                    alt="right"
                    onClick={() => nextHandler()}
                />
            </div>

            {/* Stack Icons */}
            <div className="stacks-icons">
                <div className="stacks-language">
                    <img src={process.env.PUBLIC_URL + language} alt="java"/>
                </div>
                <div className="stacks-tech">
                    {
                        size > 600 ? 
                        ( tech_stack.map((stack, i) => {
                            return <img key={i} src={process.env.PUBLIC_URL + stack} alt={"icon-" + i}/>
                        }) ) 
                        : 
                        ( tech_stack.slice(0, 3).map((stack, i) => {
                            return <img key={i} src={process.env.PUBLIC_URL + stack} alt={"icon" + i}/>
                        }) )
                    }
                </div>
            </div>

        </div>
    )
}

export default Stack