import React from 'react'

import './Footer.css'
import socials from '../socials-data';

const Footer = () => {
    return (
        <footer className="footer">
            <div className="footer-container">
                <h2 className="subheader-dark-brown">
                    You can find me on:
                </h2>
                <div className="footer-links">
                    {
                        socials.map((social) => {
                            const { id, name, image, link } = social
                            return (
                                <SocialLinks
                                    key={id}
                                    name={name}
                                    image={image}
                                    link={link}
                                />
                            )
                        })
                    }
                </div>
                <h3 className="subheader-dark-brown">
                    or drop me an email at:
                </h3>
                <div className="footer-email">
                    <a href="mailto:daffaarravi@gmail.com">
                    daffaarravi@gmail.com
                    </a>
                </div>
            </div>
        </footer>
    )
}

const SocialLinks = ({ name, image, link }) => {
    return (
        <a className="footer-link" href={link} target="_blank" rel="noreferrer">
            <img src={process.env.PUBLIC_URL + image} alt={name}/>
            <span>{name}</span>
        </a>
    )
}

export default Footer
