import React from 'react'
import Fade from 'react-reveal/Fade'

import './Projects.css'
import projects from '../projects-data'

const Projects = () => {

    // component
    return (
        <section className="projects">
            <h2 className="subheader-white">What i've been working on..</h2>
            <Fade bottom>
            <div className="projects-container">
                {   
                    //projects
                    projects.map((project) => {
                        //destructure
                        const { id, name, type, image, links } = project

                        // component
                        return (
                            <Project 
                                key={id} 
                                name={name}
                                type={type}
                                image={image}
                                links={links}
                            />
                        )
                    })
                }    
            </div>
            </Fade>
        </section>
    )
}

const Project = ({ name, type, image, links }) => {
    
    // destructure links
    const { github, behance, live } = links

    // component
    return (
        <div className="project-container">
            <img src={process.env.PUBLIC_URL + image} alt={name}/>
            <h3>{name}</h3>
            <div className="project-status">
                <h4>{type}</h4>
            </div>
            <h5>Available on:</h5>
            <div className="project-links">
                {   // Github
                    (!github && !behance) && !live ? (
                        <span>Coming soon..</span>
                    ) : (
                        <>
                            <a href={github}>Github</a>
                            <span>&nbsp;&nbsp; | &nbsp;&nbsp;</span>
                            {   // Behance
                                behance && <a href={behance}>Behance</a>
                            }
                            {   // Live
                                live && (
                                    <>
                                    <span>&nbsp;&nbsp; | &nbsp;&nbsp;</span>
                                    <a href={live}>Live</a>
                                    </>
                                ) 
                            }
                        </>
                    )
                }
            </div>
        </div>
    )
}

export default Projects
